<template>
    <v-row class="ma-5">
        <v-col cols="6">
            <v-text-field variant="outlined"
                        persistent-hint
                        v-on:keydown.enter="clickItem(filteredList[0])" 
                        label="Doorzoek Lijst..." 
                        v-model="search" 
                        hint="Voeg meerdere filters toe met een komma, enter om bovenste te selecteren"></v-text-field>
            <v-btn @click="getLLSearch(search)">Request Data</v-btn>
            <div>
                send string: 
            {{last_requested}}
            </div>
            <v-row>
                <v-col>
                    Targets: 
                    <v-list class="box list scrollable">
                        <v-list-item v-for="(target, index) in target_data" :key="index"  @click="selectTarget(target.id)">
                            <v-list-item-title>
                                <span>{{target.id}}  {{target.label}}</span>
                            </v-list-item-title>
                        </v-list-item>
                    </v-list>
                </v-col>
                <v-col>
                    Questions: 
                    <v-list class="box list scrollable">
                        <v-list-item v-for="(mcq, index) in question_data" :key="index" @click="selectQuestion(mcq.id)">
                            <v-list-item-title>
                                <span>{{mcq.id}} {{mcq.question_text}}</span>
                                        
                            </v-list-item-title>
                        </v-list-item>
                    </v-list>
                </v-col>
            </v-row>

        </v-col>
        <v-col cols="6">
            <v-card>
                <div v-if="selected.type != ''">
                    <div v-if="selected.type == 'target'">
                        <v-expansion-panels v-model="panel">
                            <TargetPanel :id="selected.id" />

                        </v-expansion-panels>
                    </div>
                    <div v-else>
                        <v-expansion-panels v-model="panel">
                            <QuestionPanel :id="selected.id" />

                        </v-expansion-panels>
                    </div>
                </div>
            </v-card>
        </v-col>

    </v-row>

</template>

<script>
import axios from 'axios'
import QuestionPanel from './components/QuestionPanel'
import TargetPanel from './components/TargetPanel'

export default {
    name: 'SearchItems',
    components: {
    QuestionPanel,
    TargetPanel,
    TargetPanel,
    QuestionPanel
},
    data (){
        return {

            search: 'test',
            response: {"status": false, "query": "", "top_k_id": []},
            last_requested: '',
            selected: {
                'id': '',
                'type': ''
            },
            panel: [0]

        }
    },
    computed: {
        question_ids(){
            const data = []
             this.response.top_k_id.forEach((e) => {
                if (this.splitId(e).type == 'question'){
                    data.push(this.splitId(e).id)
                }
            })
            return data
        },
        target_ids(){
            const data = []
            this.response.top_k_id.forEach((e) => {
                if (this.splitId(e).type == 'target'){
                    data.push(this.splitId(e).id)
                }
            })
            return data
        },
        target_data(){
            return this.target_ids.map((e) => {
                return this.$store.getters.targetDataFromId(e)
            })
        },
        question_data(){
            return this.question_ids.map((e) => {
                return this.$store.getters.mcqDataFromId(e)
            })
        },

    },
    methods: {
        getLLSearch: async function(string){
            const data = {
                "query": string.toString()
            }
            const response = axios.post('https://leerlevels-opdrachtscan.herokuapp.com/LL_search_engine', data).then((response) => {
                console.log(response)
                this.response = response.data
                this.last_requested = string
                return response
            })
            return response
        },
        splitId: function(id){
            const data ={}
            data.type = id.slice(0,1) == 'T' ? 'target' : 'question'
            data.id = id.slice(1)
            return data
        },
        selectQuestion: function(id){
            this.selected.id = id
            this.selected.type = 'question'
        },
        selectTarget: function(id){
            this.selected.id = id
            this.selected.type = 'target'
        }
    },

}
</script>

<style>
.scrollable{
  overflow-y: scroll;
}
.list{
  border: 1px solid steelblue;
  padding: 5px;
  margin: 2px;  
}
.box {
    border: solid;
    outline-color: gray;
    border-width: 1px;
    padding: 3px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: smaller;
}
span {
    display: inline-block;
    overflow:hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    font-size: 93%;
}
</style>